<template>
	<div>
		<h1>Task List</h1>
		<button @click="showCreateModal">Create Task</button>

		<ul>
			<li v-for="task in tasks" :key="task.id">
				{{ task.title }} - {{ task.description }}
				<button @click="showEditModal(task)">Edit</button>
				<button @click="deleteTask(task.id)">Delete</button>
			</li>
		</ul>

		<!-- Модальное окно для создания задачи -->
		<div v-if="showCreateTaskModal">
			<div>
				<h2>Create Task</h2>
				<input v-model="newTask.title" placeholder="Title" />
				<textarea
					v-model="newTask.description"
					placeholder="Description"
				></textarea>
				<button @click="createTask">Save</button>
				<button @click="cancelCreate">Cancel</button>
			</div>
		</div>

		<!-- Модальное окно для редактирования задачи -->
		<div v-if="showEditTaskModal">
			<div>
				<h2>Edit Task</h2>
				<input v-model="editTask.title" placeholder="Title" />
				<textarea
					v-model="editTask.description"
					placeholder="Description"
				></textarea>
				<button @click="updateTask">Save</button>
				<button @click="cancelEdit">Cancel</button>
			</div>
		</div>
	</div>
</template>
  
  <script>
import { ref, reactive } from 'vue'
import { useStore } from 'vuex'

export default {
	name: 'TaskList',
	setup() {
		const store = useStore()
		const showCreateTaskModal = ref(false)
		const showEditTaskModal = ref(false)
		const newTask = reactive({
			title: '',
			description: ''
		})
		const editTask = reactive({
			id: '',
			title: '',
			description: ''
		})

		const tasks = store.state.tasks

		const showCreateModal = () => {
			showCreateTaskModal.value = true
			showEditTaskModal.value = false // Закрываем окно редактирования задачи
		}

		const cancelCreate = () => {
			showCreateTaskModal.value = false
		}

		const createTask = () => {
			store.commit('addTask', { ...newTask, id: Date.now() })
			newTask.title = ''
			newTask.description = ''
			showCreateTaskModal.value = false
		}

		const showEditModal = (task) => {
			editTask.id = task.id
			editTask.title = task.title
			editTask.description = task.description
			showEditTaskModal.value = true
			showCreateTaskModal.value = false // Закрываем окно создания задачи
		}

		const cancelEdit = () => {
			showEditTaskModal.value = false
		}

		const updateTask = () => {
			store.commit('updateTask', { id: editTask.id, updatedTask: { ...editTask } })
			showEditTaskModal.value = false
		}

		const deleteTask = (id) => {
			store.commit('deleteTask', id)
		}

		return {
			tasks,
			showCreateTaskModal,
			showEditTaskModal,
			newTask,
			editTask,
			showCreateModal,
			cancelCreate,
			createTask,
			showEditModal,
			cancelEdit,
			updateTask,
			deleteTask
		}
	}
}
  </script>
  